// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}
@mixin list-inline{
    @include nl;

    li{
        display: inline-block;
    }
}

@mixin margin-between-elements($margin, $side: 0){
    margin-left: $margin;
    margin-right: $margin;

    &:first-child{
        margin-left: $side;
    }
    &:last-child{
        margin-right: $side;
    }
}
@mixin container($size){
    max-width: $size;
    width: 100%;
    margin: 0 auto;
    position: relative;
}

@mixin padding-x($size){
    padding-top: $size;
    padding-bottom: $size;
}
@mixin padding-y($size){
    padding-left: $size;
    padding-right: $size;
}
@mixin margin-x($size){
    margin-top: $size;
    margin-bottom: $size;
}
@mixin margin-y($size){
    margin-left: $size;
    margin-right: $size;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;
    text-justify: newspaper;
}

