// Common styles of the project

html, body {
    height: 100%;
}

.page {
    background-image: url("%=static=%general/bg.jpg");
    color: white;
    font-family: 'Open Sans', sans-serif;
}

.video_bg{
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}
.page__wrapper {
    min-height: 100%;
    position: fixed;
    bottom: 0;
    background: rgba(0, 0, 0, 0.85);
    width: 100%;
}

* html .page__wrapper {
    height: 100%;
}

.page__buffer {
    height: 50px;
}

.square{
    &-bg{
        background-size: contain;
        width: 200px;
        height: 200px;
        position: absolute;
        z-index: 10;
        background-repeat: no-repeat;
    }
    &1{
        background-image: url("%=static=%general/sq/sq1.png");
        bottom: 10%;
        left: 10%;
    }
    &2{
        background-image: url("%=static=%general/sq/sq2.png");
        top: 10%;
        right: 10%;
    }
}

// header
.header{
    display: flex;
    justify-content: space-between;
    @include padding-x(14px);
    line-height: 35px;
    a{
        letter-spacing: 1px;
    }
}
.logo{
    span{
        vertical-align: top;
        margin-left: 20px;
    }
}

.menu{
    @include list-inline;
    li{
        @include margin-between-elements(36px)
    }
}

.social{
    @include list-inline;
    li{
        @include margin-between-elements(18px)
    }
}

// content
.content{
    color: $c-text;
    height: calc(100vh - 130px);
    overflow-y: scroll;
    h1{
        font-weight: bold;
        font-size: 288px;
        text-align: center;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #cccccc14;

        &.title{
            &-medium{
                font-size: 200px;
                line-height: 200px;
                margin: 0;
            }
        }
    }
}
.container{
    @include padding-y(100px);
    &--small{
        @include container(1240px)

    }
}

.play-icon{
    position: relative;
    display: block;
    margin: 0 auto;
    margin-top: 50px;
    width: 100%;

    div{
        background-repeat: no-repeat;
        background-size: contain;
        position: relative;
    }

    &-big{
        background-image: url("%=static=%content/play/1.svg");
        width: 459px;
        height: 532px;
        margin: 0 auto;
    }
    &-middle{
        background-image: url("%=static=%content/play/2.svg");
        top: 137px;
        left: 85px;
        width: 223px;
        height: 258px;
    }
    &-small{
        background-image: url("%=static=%content/play/3.svg");
        top: 92px;
        left: 59px;
        width: 64.5px;
        height: 74.5px;

        &.alt{
            position: absolute;
        }
    }

}



.bottom-text{
    bottom: 100px;
    position: absolute;
}

.play{
    &__bg-tr{
        background-image: url("%=static=%content/play/1.svg");
        background-repeat: no-repeat;
        background-size: cover;
        width: 512px;
        height: 595px;
        position: absolute;

        &.num-1{
            transform: rotate(-84deg);
            top: -201px;
            left: 167px;
        }
        &.num-2{
            transform: rotate(55deg);
            left: 786px;
            top: 263px;
        }
        &.num-3{
            background-image: url("%=static=%content/play/4.svg");
            top: 435px;
            left: 64px;
            width: 575px;
            height: 435px;
        }
    }
    &__item{
        display: inline-block;
        position: relative;
        &-1{
            left: 134px;
            top: 72px;
        }
        &-2{
            left: 324px;
            top: 347px;
        }
        &-3{
            top: 235px;
            left: 0;
        }

        &-data{
            display: inline-block;
            position: relative;
            top: 84px;
            vertical-align: top;
        }
        &-num{
            display: inline-block;
            font-size: 250px;
            height: 255px;
            color: #cccccc14;
            font-weight: bold;
            vertical-align: top;
            line-height: 190px;
            width: 156px;
        }
        h3{
            letter-spacing: 2.5px;
            margin-top: 0;
            margin-bottom: 2px;
            font-size: 21px;
            color: $c-orange;
            font-weight: normal;
        }
        p{
            font-size: 17px;
            margin-top: 0;
            margin-bottom: 12px;
            color: $c-gray-dark;
            letter-spacing: 1px;
            font-weight: 600;
        }
    }
}

.shop{
    &__item{
        display: flex;
        height: calc(100vh - 130px);


        &-image{
            display: inline-block;
            img{
                height: 500px;
            }
        }
        &-title{
            font-size: 30px;
            height: 40px;
            padding-top: 8px;
            b{
                font-size: 60px;
            }
        }
        &-data{
            padding: 150px 0 40px 0;
        }
        &-price{
            font-size: 36px;
            font-weight: bold;
            padding-left: 40px;
            padding-right: 50px;
            position: relative;
            top: 10px;
            height: 30px;
        }
        &-params{
            li{
                line-height: 180%;
            }
        }
        &-bottom{
            display: flex;
        }

        &-white{
            color: $c-white;
            .gradient{
                background: -webkit-linear-gradient($c-white 50%, #000000);
            }
        }
        &-green{
            color: $c-green;
            .gradient{
                background: -webkit-linear-gradient($c-green 50%, #000000);
            }
        }
        &-orange{
            color: $c-orange;
            .gradient{
                background: -webkit-linear-gradient($c-orange 50%, #000000);
            }
        }
        &-gold{
            color: $c-yellow;
            .gradient{
                background: -webkit-linear-gradient($c-yellow 50%, #000000);
            }
        }
        .gradient{
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
}
