// GUI stylies of the project
// For example, styles of the links, buttons and so on.

.btn{
    background: $c-orange-btn;
    border-radius: 10px;
    border: none;
    height: 45px;
    padding: 0 60px;
    font-size: 22px;
    line-height: 45px;
    letter-spacing: 2px;
    align-items: center;
    text-align: center;
    font-style: normal;
    color: white;
    display: inline-block;
    &:hover{
        cursor: pointer;
        background: $c-orange-btn-gradient;
        color: white;
    }

    &--fixed{
        width: 161px;
        padding: 0;
        text-align: center;
    }
}

a{
    color: $c-white;
    text-decoration: none;

    &:hover{
        color: $c-orange;
    }

    &.active{
        color: $c-orange;
    }
}

body{
    line-height: 25px;
}
