.btn {
  background: #F04A2A;
  border-radius: 10px;
  border: none;
  height: 45px;
  padding: 0 60px;
  font-size: 22px;
  line-height: 45px;
  letter-spacing: 2px;
  align-items: center;
  text-align: center;
  font-style: normal;
  color: white;
  display: inline-block;
}

.btn:hover {
  cursor: pointer;
  background: linear-gradient(103.99deg, #F47D22 -11.79%, #F04A2A 21.54%, #F58222 133.66%);
  color: white;
}

.btn--fixed {
  width: 161px;
  padding: 0;
  text-align: center;
}

a {
  color: #ffffff;
  text-decoration: none;
}

a:hover {
  color: #E24728;
}

a.active {
  color: #E24728;
}

body {
  line-height: 25px;
}

html, body {
  height: 100%;
}

.page {
  background-image: url("../img/general/bg.jpg");
  color: white;
  font-family: 'Open Sans', sans-serif;
}

.video_bg {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.page__wrapper {
  min-height: 100%;
  position: fixed;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  width: 100%;
}

* html .page__wrapper {
  height: 100%;
}

.page__buffer {
  height: 50px;
}

.square-bg {
  background-size: contain;
  width: 200px;
  height: 200px;
  position: absolute;
  z-index: 10;
  background-repeat: no-repeat;
}

.square1 {
  background-image: url("../img/general/sq/sq1.png");
  bottom: 10%;
  left: 10%;
}

.square2 {
  background-image: url("../img/general/sq/sq2.png");
  top: 10%;
  right: 10%;
}

.header {
  display: flex;
  justify-content: space-between;
  padding-top: 14px;
  padding-bottom: 14px;
  line-height: 35px;
}

.header a {
  letter-spacing: 1px;
}

.logo span {
  vertical-align: top;
  margin-left: 20px;
}

.menu {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside;
}

.menu li {
  display: inline-block;
}

.menu li {
  margin-left: 36px;
  margin-right: 36px;
}

.menu li:first-child {
  margin-left: 0;
}

.menu li:last-child {
  margin-right: 0;
}

.social {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside;
}

.social li {
  display: inline-block;
}

.social li {
  margin-left: 18px;
  margin-right: 18px;
}

.social li:first-child {
  margin-left: 0;
}

.social li:last-child {
  margin-right: 0;
}

.content {
  color: #EDEDED;
  height: calc(100vh - 130px);
  overflow-y: scroll;
}

.content h1 {
  font-weight: bold;
  font-size: 288px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #cccccc14;
}

.content h1.title-medium {
  font-size: 200px;
  line-height: 200px;
  margin: 0;
}

.container {
  padding-left: 100px;
  padding-right: 100px;
}

.container--small {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.play-icon {
  position: relative;
  display: block;
  margin: 0 auto;
  margin-top: 50px;
  width: 100%;
}

.play-icon div {
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}

.play-icon-big {
  background-image: url("../img/content/play/1.svg");
  width: 459px;
  height: 532px;
  margin: 0 auto;
}

.play-icon-middle {
  background-image: url("../img/content/play/2.svg");
  top: 137px;
  left: 85px;
  width: 223px;
  height: 258px;
}

.play-icon-small {
  background-image: url("../img/content/play/3.svg");
  top: 92px;
  left: 59px;
  width: 64.5px;
  height: 74.5px;
}

.play-icon-small.alt {
  position: absolute;
}

.bottom-text {
  bottom: 100px;
  position: absolute;
}

.play__bg-tr {
  background-image: url("../img/content/play/1.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 512px;
  height: 595px;
  position: absolute;
}

.play__bg-tr.num-1 {
  transform: rotate(-84deg);
  top: -201px;
  left: 167px;
}

.play__bg-tr.num-2 {
  transform: rotate(55deg);
  left: 786px;
  top: 263px;
}

.play__bg-tr.num-3 {
  background-image: url("../img/content/play/4.svg");
  top: 435px;
  left: 64px;
  width: 575px;
  height: 435px;
}

.play__item {
  display: inline-block;
  position: relative;
}

.play__item-1 {
  left: 134px;
  top: 72px;
}

.play__item-2 {
  left: 324px;
  top: 347px;
}

.play__item-3 {
  top: 235px;
  left: 0;
}

.play__item-data {
  display: inline-block;
  position: relative;
  top: 84px;
  vertical-align: top;
}

.play__item-num {
  display: inline-block;
  font-size: 250px;
  height: 255px;
  color: #cccccc14;
  font-weight: bold;
  vertical-align: top;
  line-height: 190px;
  width: 156px;
}

.play__item h3 {
  letter-spacing: 2.5px;
  margin-top: 0;
  margin-bottom: 2px;
  font-size: 21px;
  color: #E24728;
  font-weight: normal;
}

.play__item p {
  font-size: 17px;
  margin-top: 0;
  margin-bottom: 12px;
  color: #666666;
  letter-spacing: 1px;
  font-weight: 600;
}

.shop__item {
  display: flex;
  height: calc(100vh - 130px);
}

.shop__item-image {
  display: inline-block;
}

.shop__item-image img {
  height: 500px;
}

.shop__item-title {
  font-size: 30px;
  height: 40px;
  padding-top: 8px;
}

.shop__item-title b {
  font-size: 60px;
}

.shop__item-data {
  padding: 150px 0 40px 0;
}

.shop__item-price {
  font-size: 36px;
  font-weight: bold;
  padding-left: 40px;
  padding-right: 50px;
  position: relative;
  top: 10px;
  height: 30px;
}

.shop__item-params li {
  line-height: 180%;
}

.shop__item-bottom {
  display: flex;
}

.shop__item-white {
  color: #ffffff;
}

.shop__item-white .gradient {
  background: -webkit-linear-gradient(#ffffff 50%, #000000);
}

.shop__item-green {
  color: #9EDEB3;
}

.shop__item-green .gradient {
  background: -webkit-linear-gradient(#9EDEB3 50%, #000000);
}

.shop__item-orange {
  color: #E24728;
}

.shop__item-orange .gradient {
  background: -webkit-linear-gradient(#E24728 50%, #000000);
}

.shop__item-gold {
  color: #E4CA60;
}

.shop__item-gold .gradient {
  background: -webkit-linear-gradient(#E4CA60 50%, #000000);
}

.shop__item .gradient {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
